@import url(https://fonts.googleapis.com/css?family=Anton&display=swap);
/* Costum fonts import*/
@font-face {
    font-family: 'OldPress';
    src: url("/sleepy-cabin/static/media/Old Press.ec1d68d6.ttf");
}

@font-face {
    font-family: 'JoeFont';
    src: url("/sleepy-cabin/static/media/Joe Font.21af3480.ttf");
}

@font-face {
    font-family: 'gravitation';
    src: url(/sleepy-cabin/static/media/gravitation_falls.0865f672.ttf);
}

/* General Rules */

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html, body {
    font-size: 100%;
    background-color: #38251F;
}

/* Container */

.container { 
    max-width: 960px;
    width: 95%;
    margin: auto;
}

.flex-column {
    display: flex;
    flex-direction: column;
}

.row-reverse {
    flex-direction: row-reverse;
}




/* Navbar i header*/
.header {
    position: absolute;
    width: 100%;
    display: flex;
    overflow: hidden;
}

.navbar {
    position: relative;
    left: 15rem;
    top: 3rem;
    height: 253px;
    width: 510px;
    max-width: 80%;
    margin:auto;
    background-image: url(/sleepy-cabin/static/media/note.621097d3.png);    
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    display: flex;    
    align-items: center;
    justify-content: center;
}

.nav-list {
    list-style-type: none;
    margin-right: 1rem;
}

.nav-item {    
    display: inline;
    margin: 0 1rem;
    font-size: 4.4rem;
    border: 0.3rem solid transparent;    
    color: #3D1D0A;
    font-family: 'OldPress', sans-serif;
}

.nav-item:hover {
    color: white;
    border: 0.3rem solid transparent;    
    border-image: url(/sleepy-cabin/static/media/circle.a9fa4b16.png) 20%;
    cursor: pointer;
    text-shadow: 5px 5px black;
}

/* Tablets */

@media only screen and (max-width: 768px) {
    .navbar {
        left: 0;
    }
}

/* Phones */

@media only screen and (max-width: 600px) {
    .navbar {
        max-width: 70%;
        height: 180px;
        left: 0;
        top: 10%;
    }

    .nav-item {
        margin: auto 0.2em;
        font-size: 2.4rem;
        font-family: 'OldPress', sans-serif;
    }    
}
/* Hero */
.hero {    
    background-image: url(/sleepy-cabin/static/media/heroBg.c3d7a3b4.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.hero-logo {
    width: 80%;
    max-width: 705px;
    z-index: 1;
}

.hero-text {
    position: absolute;
    top: 60%;
    left: 50%;
    text-align: center;
    color: white;
    font-family: 'JoeFont';
    font-size: 3.8em;
    font-weight: bold;
    text-shadow: 5px 2px black;
    z-index: 2;
}


/* Tablets */
@media only screen and (max-width: 768px) {
    .hero-text {
        left: 40%;
        font-size: 2.8rem;
        margin-right: 1em;
        top: 65%;
    }
}

/* Phones */
@media only screen and (max-width: 600px) {
    .hero-text {
        font-size: 2rem;
        top: 70%;
        left: 0;
        width: 100%;
    }
    
}
/* Cards */
.text-with-photo {
    display: flex;
    align-items: center;
    margin: 2rem 0;
}

.photo {
    width: 50%;
    max-width: 500px;
}

.card-text {
    width: 50%;
}

.title-container {
    max-height: 100%;
    height: 200px;
    background-image: url(/sleepy-cabin/static/media/titleSignDark.51a0d572.png);
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    display: flex;
    align-items: center;
    justify-content: center;
}

.sign-reverse{
    background-image: url(/sleepy-cabin/static/media/titleSignDarkFliped.1ab59023.png);
}
.title { 
    /* background-color: rgba(255,255,255 , 0.1);
        border-radius: 8%;
    */
    margin-bottom: 0.5em;
    text-align: center;
    font-weight: 80;
    font-size: 1.8rem;
    color: #E3E3E3;
    letter-spacing: 0.1rem;
    font-family: 'gravitation';
    text-transform: uppercase;
    z-index: 1;

    text-shadow:
    -2px -2px 0 #000,  
     2px -2px 0 #000,
     -2px 2px 0 #000,
      2px 2px 0 #000;
}

.description {
    font-family: 'JoeFont', sans-serif;
    margin: 0;
    color: lightgray;
    font-size: 1.3rem;
    font-weight: 700;
    text-shadow:
    -2px -2px 0 #000,  
     2px -2px 0 #000,
     -2px 2px 0 #000,
      2px 2px 0 #000;
}

/* Tablets */

@media only screen and (max-width: 768px) {
    .description {
        font-size: 1rem;
    }
    .title {
        font-size: 1.8rem;
    }
}

/* Phones */

@media only screen and (max-width: 600px) {
    .text-with-photo {
        flex-direction: column;
    }

    .card-text {
        width: 90%;
    }

    .photo {
        width: 90%;
    }    
}
/* TBD */
.featured-header {
    width: 100%;    
    height: 300px;
    background-image: url(/sleepy-cabin/static/media/hrSketch.2094b860.png);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
}

.featured-header h1 {
    font-size: 8rem;
    font-weight: 80;
    font-family: 'gravitation', sans-serif;
    text-shadow: 5px 5px black;
    color: #E3E3E3;
}

.featured-rooms {
    display: flex;
    height: auto;
    margin-top: 3em;
    align-items: center;
}

.room-img {
    max-width: 600px;
    width: 100%;
}

.room {
    text-decoration: none;
}

.room-name {
    position: relative;
    text-align: center;
    top: -3em;
    font-family: 'JoeFont', sans-serif;
    margin: 0;
    color: black;
    font-size: 1.7rem;
    font-weight: 700;
    z-index: 1;
}

/* Tablets */

@media only screen and (max-width: 768px) {
    .featured-header h1 {
        font-size: 5rem;
    }
    .room-name {
        font-size: 2rem;
    }
    .featured-rooms {
        display: flex;
        flex-direction: column;
        height: auto;
        align-items: center;
    }
}

/* Phones */

@media only screen and (max-width: 600px) {
    .featured-header h1 {
        font-size: 4rem;
    }
    .room-name {
        font-size: 1.8rem;
    }
    .featured-rooms {
        flex-direction: column;
        height: auto;
    }
}
.amenities {
    display: flex;
    width: 100%;
    margin-top: 3em;
}

.amenities-header {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 250px;
    font-size: 4.5rem;
    text-align: center;
    font-weight: 500;
    letter-spacing: 0.5rem;
    font-family: 'gravitation', sans-serif;
    text-shadow: 5px 5px black;
    color: #E3E3E3; 
    /*background-color: #916150; */
}


.amenity {
    width: 25%;
    text-align: center;
    margin: 0 1em;
}

.amenity-icon {
    font-size: 6rem;
    color: #EEB08C;
    margin: 0.5em 0.5em;

}
.amenity-header {
    font-family: 'JoeFont', sans-serif;
    margin: 0;
    color: lightgray;
    font-size: 2rem;
    font-weight: 700;
    text-shadow:
    -2px -2px 0 #000,  
     2px -2px 0 #000,
     -2px 2px 0 #000,
      2px 2px 0 #000;
}
.amenity-desc {
    font-family: 'JoeFont', sans-serif;
    margin-top: 1em;
    color: lightgray;
    font-size: 1rem;
    font-weight: 700;
    text-shadow:
    -2px -2px 0 #000,  
     2px -2px 0 #000,
     -2px 2px 0 #000,
      2px 2px 0 #000;
}


@media only screen and (max-width: 768px) {
    .amenities-header {
        font-size: 4rem;
    }

    .amenities {
        display: grid;
        width: 100%;
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 1em;
    }

    .amenity {
        padding: 1em;
        display: flex;
        flex-direction: column;
        text-align: center;
        width: 100%;
    }

    .amenity-icon {
        margin: auto;
    }
}

/* Phones */

@media only screen and (max-width: 600px) {
    .amenities-header {
        font-size: 1.5rem;
    }

    .amenities {
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        grid-gap: 1em;
    }

    .amenity {
        display: flex;
        flex-direction: column;
        text-align: center;
        margin: 0;
        width: 100%;
        align-items: center;
    }

}
